import React from 'react';
import { useSelector, useDispatch } from "react-redux";
import { sport_Yes, sport_No, other } from "../../actions";
import { useNavigate } from "react-router-dom";
import { Button } from '@chakra-ui/react';
import "./styles.css";

// URL
const homeUrl = process.env.PUBLIC_URL;

const Sport = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // Redux
  const gender = useSelector((state) => state.gender); //
  const age = useSelector((state) => state.age); //


  // 
  const sport_Yes_handleClick = () => {
    dispatch(sport_Yes()); // sport_Yesdispatch
    navigate(`${homeUrl}/result`); // 
  }

  // 
  const sport_No_handleClick = () => {
    dispatch(sport_No()); // sport_Nodispatch
    dispatch(other())
    navigate(`${homeUrl}/result`); // 
  }

  return (
    <>
      <h3>{gender[3]}</h3>
      <h3>{age[2]}</h3>
      <h2></h2>
      <Button onClick={() => sport_Yes_handleClick()} style={{ fontSize: '1.5em' }} className='yesButton'></Button>
      <Button onClick={() => sport_No_handleClick()} style={{ fontSize: '1.5em' }}></Button>
      <br/>
      <br/>
      <Button onClick={() => navigate(`${homeUrl}/exercise`)} style={{ fontSize: '1.5em' }}></Button>
    </>
  );
}

export default Sport;
