import React from 'react'
import { useSelector, useDispatch } from "react-redux"
import { kid, lowGrade, middleGrade, highGrade, notSelect_Age, } from "../../actions"
import { useNavigate } from "react-router-dom"
import { Button } from '@chakra-ui/react'
import "./styles.css";

// URL
const homeUrl = process.env.PUBLIC_URL;

const Age = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const gender = useSelector((state) => state.gender); // 

  // 
  const kid_handleClick = () => {
    dispatch(kid()); // 4,6,
    navigate(`${homeUrl}/exercise`); // 
  }
  
  // 
  const low_handleClick = () => {
    dispatch(lowGrade()); // 6,8,
    navigate(`${homeUrl}/exercise`); // 
  }
  
  // 
  const middle_handleClick = () => {
    dispatch(middleGrade()); // 8,10,
    navigate(`${homeUrl}/exercise`); // 
  }
  
  // 
  const high_handleClick = () => {
    dispatch(highGrade()); // 10,12.
    navigate(`${homeUrl}/exercise`); // 
  }
  
  // 
  const skip_handleClick = () => {
    dispatch(notSelect_Age()); // 1,15,
    navigate(`${homeUrl}/exercise`); // 
  }

  return (
    <>
      <h3>{gender[3]}</h3>
      <h2></h2>
      <div>
        <Button onClick={() => kid_handleClick()} style={{ fontSize: '1.5em' }}>36</Button>
      </div>
      <br/>
        <Button onClick={() => low_handleClick()} style={{ fontSize: '1.5em' }}>68</Button>
      <br/>
      <br/>
        <Button onClick={() => middle_handleClick()} style={{ fontSize: '1.5em' }}>810</Button>  
      <br/>
      <br/>
        <Button onClick={() => high_handleClick()} style={{ fontSize: '1.5em' }}>1012</Button>
      <br/>
      <br/>
      <Button onClick={() => skip_handleClick()} style={{ fontSize: '1.5em' }}></Button>
      <br/>
      <br/>
      <Button onClick={() => navigate(`${homeUrl}/gender`)}style={{ fontSize: '1.5em' }}></Button>
    </>
  );
}

export default Age;





