import React from 'react';
import { useDispatch } from "react-redux";
import { boy, girl, all } from "../../actions";
import { useNavigate } from "react-router-dom";
import { Button } from '@chakra-ui/react';
import "./styles.css";

const homeUrl = process.env.PUBLIC_URL;

const CharacterKeyword = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // 
  const boy_handleClick = () => {
    dispatch(boy()); // boydispatch
    navigate(`${homeUrl}/age`); // 
  }

  // 
  const girl_handleClick = () => {
    dispatch(girl()); // girldispatch
    navigate(`${homeUrl}/age`); // 
  }

  // 
  const skip_handleClick = () => {
    dispatch(all()); // alldispatch
    navigate(`${homeUrl}/age`); // 
  }

  return (
    <>
      <h2></h2>
      <Button onClick={() => boy_handleClick()} style={{ fontSize: '1em' }}></Button>
      <Button onClick={() => girl_handleClick()} style={{ fontSize: '1em' }}></Button>
      <Button onClick={() => skip_handleClick()} style={{ fontSize: '1em' }}></Button>
      <Button onClick={() => navigate(`${homeUrl}/firstchoice`)} style={{ fontSize: '1em' }}></Button>
    </>
  );
}

export default CharacterKeyword