import React from 'react';
import { useDispatch } from "react-redux";
import { boy, girl, all } from "../../actions";
import { useNavigate } from "react-router-dom";
import { Button } from '@chakra-ui/react';
import "./styles.css";

// URL
const homeUrl = process.env.PUBLIC_URL;

const Gender = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // 
  const boy_handleClick = () => {
    dispatch(boy()); // boydispatch
    navigate(`${homeUrl}/age`); // 
  }

  // 
  const girl_handleClick = () => {
    dispatch(girl()); // girldispatch
    navigate(`${homeUrl}/age`); // 
  }

  // 
  const skip_handleClick = () => {
    dispatch(all()); // alldispatch
    navigate(`${homeUrl}/age`); // 
  }

  return (
    <>
      <br/>
      <h2></h2>
      <br/>
      <Button onClick={() => boy_handleClick()} style={{ fontSize: '2.5em' }} className='boyButton'></Button>
      <Button onClick={() => girl_handleClick()} style={{ fontSize: '2.5em' }} className='girlButton'></Button>
      <br/>
      <br/>
      <br/>
      <div>
        <Button onClick={() => skip_handleClick()} style={{ fontSize: '1.5em' }} className='buttonRadius'></Button>
        <br/>
        <br/>
        <Button onClick={() => navigate(`${homeUrl}/firstchoice`)} style={{ fontSize: '1.5em' }}></Button>
      </div>
    </>
  );
}

export default Gender;



