import React from 'react'
import { useSelector, useDispatch } from "react-redux"
import { exercise_Yes , exercise_No } from "../../actions"
import { useNavigate } from "react-router-dom"
import { Button } from '@chakra-ui/react'
import "./styles.css";

// URL
const homeUrl = process.env.PUBLIC_URL;

const Exercise = () => {

  const navigate = useNavigate();
  const dispatch = useDispatch();

  // useSelectorRedux
  const gender = useSelector((state) => state.gender); // 
  const age = useSelector((state) => state.age); // 

  // 
  const exercise_Yes_handleClick = () => {
    dispatch(exercise_Yes()); // 1 1resultfilter
    navigate(`${homeUrl}/sport`); // 
  }

  // 
  const exercise_No_handleClick = () => {
    dispatch(exercise_No()); // 22resultfilter
    navigate(`${homeUrl}/game`); // 
  }
  

  return (
    <>
      <h3>{gender[3]}</h3>
      <h3>{age[2]}</h3>
      <h2></h2>
      <Button onClick={() => exercise_Yes_handleClick()} style={{ fontSize: '1.5em' }} className='yesButton'></Button>
      <Button onClick={() => exercise_No_handleClick()} style={{fontSize: '1.5em'}}></Button>
      <br/>
      <br/>
      <div>
        <Button onClick={() => navigate(`${homeUrl}/age`)}style={{ fontSize: '1.5em' }}></Button>
      </div>
    </>
  );
}

export default Exercise;
