import React from 'react';
import { useSelector, useDispatch } from "react-redux";
import { videogame_Yes, videogame_No, other } from "../../actions";
import { useNavigate } from "react-router-dom";
import { Button } from '@chakra-ui/react';
import "./styles.css";

// URL
const homeUrl = process.env.PUBLIC_URL;

const Videogame = () => {
  // Redux
  const gender = useSelector((state) => state.gender); //
  const age = useSelector((state) => state.age); //

  const navigate = useNavigate();
  const dispatch = useDispatch();

  // 
  const videogame_Yes_handleClick = () => {
    dispatch(videogame_Yes()); // videogame_Yesdispatch
    navigate(`${homeUrl}/result`); // 
  }

  // 
  const videogame_No_handleClick = () => {
    dispatch(videogame_No()); // videogame_Nodispatch
    dispatch(other())
    navigate(`${homeUrl}/result`); // 
  }

  return (
    <>
      <h3>{gender[3]}</h3>
      <h3>{age[2]}</h3>
      <h2></h2>
      <Button onClick={() => videogame_Yes_handleClick()} style={{ fontSize: '1.5em' }} className='yesButton'></Button>
      <Button onClick={() => videogame_No_handleClick()} style={{ fontSize: '1.5em' }}></Button>
      <br/>
      <br/>
      <Button onClick={() => navigate(`${homeUrl}/game`)} style={{ fontSize: '1.5em' }}></Button>
    </>
  );
}

export default Videogame;

